import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SaveService } from '@app/core/http/save.service';
import { GrammarCreate } from '@app/shared/models/vocab';

@Component({
  selector: 'app-grammar-create',
  templateUrl: './grammar-create.component.html',
  styleUrls: ['./grammar-create.component.css']
})
export class GrammarCreateComponent implements OnInit {
  public submitted: boolean = false;

  createForm: FormGroup = this.fb.group({
    name: ["", [Validators.required]],
    definition: ["", Validators.required],
    questions: this.fb.array([]),
  });
  public questionId: string;

  constructor(private fb: FormBuilder,private _save:SaveService,private _activeRoute:ActivatedRoute) { }

  ngOnInit() {
    this.questionId = this._activeRoute.snapshot.paramMap.get("questionId");

    let data = this._activeRoute.snapshot.data['data'];
    if (data)
      this.getDetail(data);
    else this.addNewQuest();

  }

  getDetail(data) {
    this.createForm = this.fb.group({
      name: [data.name, [Validators.required]],
      definition: [data.definition, [Validators.required]],
      questions: this.fb.array([]),
    });
    this.objectArray(data.questions);
  }

  objectArray(questions) {
    console.log(questions)
    let arr: any = [];

    questions.forEach((element) => {
      const lessonForm = this.fb.group({
        question: [element.question, Validators.required],
        explanation: [element.explanation, Validators.required],
      });
      this.questions.push(lessonForm);
    });
  }

  get questions() {
    return this.createForm.controls["questions"] as FormArray;
  }

  addNewQuest() {
    const lessonForm = this.fb.group({
      question: ['', Validators.required],
      explanation: ['', Validators.required],
    });
    this.questions.push(lessonForm);
  }

  get f() {
    return this.createForm.controls;
  }

  deleteQuestion(index: number) {
    this.questions.removeAt(index);
  }

  onCreate() {
    this.submitted = true;
    if (this.createForm.invalid) {
      return;
    } else {

      this._save.onSaveGrammarQuestion(this.createForm.value, this.questionId)
        .subscribe((res) => {
          this._save.goBackPrev();
        })
    }

  }
}
